import {platform} from '@/utils/platform'
import {compVer} from '@/utils/tools'

export default {
    methods: {
        //app中页面可见
        visibilityAppShow(fun){
            if(!platform.isApp) return;
            let needProcess = compVer(platform.appVersion, '8.29.1') <= 0;
            let count = 0;
            this.eventListenerApp('APP_E5', ()=>{
                if(platform.isIos && needProcess){
                    count++;
                    if(count === 2){
                        count = 0;
                        fun && fun();
                    }
                }else{
                    fun && fun();
                }
            });
            if(platform.isIos){
                this.eventListenerApp('APP_E1', ()=>{
                    fun && fun();
                });
            }
        },
        //web中页面可见
        visibilityWebChange(fun){
            let hiddenProperty = 'hidden' in document ? 'hidden' :
                'webkitHidden' in document ? 'webkitHidden' :
                    'mozHidden' in document ? 'mozHidden' :
                        null;
            let visibilityChangeEvent = hiddenProperty.replace(/hidden/i, 'visibilitychange');
            document.addEventListener(visibilityChangeEvent, () => {
                if (!document[hiddenProperty]) {
                    //出现
                    fun && fun(true)
                } else {
                    //隐藏
                    fun && fun(false)
                }
            });
        },
    }
}