<template>
    <div class="vip-pay-result">
        <div class="result-success" v-if="pay_status===1">
            <div class="success-content">
                <div class="portrait">
                    <img :src="user_info.avatar||DEFAULT_USER_AVATAR_NEW" alt=""/>
                </div>
                <div class="success-title" v-if="user_status===1">尊敬的绿卡会员，欢迎加入！</div>
                <div class="success-title" v-if="user_status===2">续卡成功！</div>
                <div class="success-title" v-if="user_status===3">尊敬的绿卡会员，欢迎回归！</div>
                <div class="success-time">
                    {{notice}}
                </div>
                <div class="success-btn" @click="gotoVipPage">
                    <span>领取绿卡专享券</span>
                    <icon-font name="Jiantou" color="#ffffff" size="36" class="suggest-icon"/>
                </div>
            </div>

        </div>

        <div class="result-fail" v-if="pay_status===0">
            <div class="fail-msg">
                <icon-font class="i-fail" name="xiadanshibai" size="30" color="#FF636A"></icon-font>
                <span>支付失败</span>
            </div>
            <div class="fail-desc">{{notice}}</div>
            <div class="fail-btn" @click="gotoVipPayPage">
                <span>重新购买</span>
            </div>
        </div>
    </div>
</template>

<script>
    import API from '@/server/api'
    import {platform} from '@/utils/platform'
    import visibility from '@/mixins/visibility'
    import {DEFAULT_USER_AVATAR_NEW} from '@/const/images'
    import {local} from '@/utils/storage'

    export default {
        name: "vip-pay-result",
        mixins: [visibility],
        data() {
            return {
                pay_status: null,  //为1的时候支付成功
                notice: '',
                user_status: 0, //1新用户，2续费用户，3已过期
                DEFAULT_USER_AVATAR_NEW,
                invite_code: '' //小哥推广码
            };
        },
        mounted() {
            let {invite_code} = this.$route.query;
            this.invite_code = invite_code||'';
            this.getVipOrderCheck();
        },
        methods: {
            async getVipOrderCheck() {
                let {user_status} = this.$route.query;
                let {order_number} = local.load('VIP_PAYING');
                if(!order_number) return;
                const {data} = await this.$http.get(API.vipApi('payCheck'), {order_number});
                if (data) {
                    const {pay_status, notice, money, order_number} = data;
                    this.log({
                        cid: 'open_vip_payresult',
                        aid: 'index',
                        value: pay_status,
                        ad_id: this.invite_code,
                        label: user_status,
                        index: money,
                        code: order_number
                    });

                    this.pay_status = pay_status;
                    this.notice = notice;
                    this.user_status = user_status;

                    this.setTitle(pay_status ? '支付成功' : '支付失败');
                    this.setApp();
                }
            },

            gotoVipPage() {
                let invite_code = this.invite_code;
                this.log({
                    cid: 'open_vip_payresult',
                    aid: 'my_vip_card',
                });
                this.openUrl({
                    path: '/vip',
                    query: {back_app: 1, invite_code}
                });
            },
            gotoVipPayPage() {
                let invite_code = this.invite_code;
                this.log({
                    cid: 'open_vip_payresult',
                    aid: 're_buy',
                });
                this.openUrl({
                    path: '/vip/pay',
                    query: {back_app: 1, invite_code}
                });
            },
            setApp() {
                if (!platform.isApp) return;
                !!this.pay_status && this.eventListenerApp('APP_E6', () => {
                    let invite_code = this.invite_code;
                    this.openUrl({path: '/vip', query: {back_app: 1, invite_code}});
                }, true);
                this.visibilityAppShow(()=>{
                    this.getVipOrderCheck();
                })
            },
        }
    }
</script>

